import { Heart, Search, ShoppingCart, User } from 'lucide-react'
import Link from 'next/link'
import React from 'react'

function Header() {
  return (
    <header className="w-full h-[80px] py-[10px] px-4 md:px-[40px] flex justify-between items-center bg-white shadow-md">
      {/* Navigation Links (Hidden on mobile, visible on medium screens and up) */}
      <nav className="hidden md:flex gap-[30px] text-[16px] font-semibold">
        <Link href={'/'}>Home</Link>
        <Link href={'/shop'}>Shop</Link>
        <Link href={'/blog'}>About</Link>
        <Link href={'/contact'}>Contact</Link>
      </nav>

      {/* Icon Links (Always visible and responsive) */}
      <div className="flex gap-[16px]">
        <Link href={'/account'}>
          <User size={22} className="hover:text-gray-600 stroke-[2]" />
        </Link>

        <Link href={'/'}>
          <Search size={22} className="hover:text-gray-600 stroke-[2]" />
        </Link>

        <Link href={"/checkout"}>
          <Heart size={22} className="hover:text-gray-600 stroke-[2]" />
        </Link>

        <Link href={"/cart"}>
          <ShoppingCart size={22} className="hover:text-gray-600 stroke-[2]" />
        </Link>
      </div>
    </header>
  )
}

export default Header
